.App {
  text-align: center;
  padding: 20px;
}

input {
  padding: 10px;
  margin: 10px;
}

button {
  padding: 10px;
  margin: 5px;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

li button {
  background-color: #f44336;
  color: white;
  border: none;
  cursor: pointer;
}

li button:hover {
  background-color: #d32f2f;
}
